
.tasks-block{
  display: flex;
  justify-content: center;
}
.tasks-block ul{
  margin-top: 20px;
  width: 300px;
  height: 200px;
  border-radius: 14px;
  cursor: pointer;
  overflow: scroll;
  overflow-x: hidden;
}
.tasks-block ul::-webkit-scrollbar {
  width: 7px;
  background-color: #223c50;
}
.tasks-block li{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 10px 10px;
  padding: 25px 10px;
  list-style: none;
  width: 280px;
  height: 40px;
  font-size: 20px;
  background-color: rgba(82, 79, 180, 0.417);
  color:#fff;
  border:1px solid #fff;
  border-radius: 10px;
  font-weight: 700;
  white-space: nowrap;
  transition: 0.3s;
}
.done{
  width: 360px;
  height: 20px;
  background-color: rgba(110, 34, 101, 0.614);
}

